import { create } from 'zustand';

export type DitheringAlgorithm = 
  | 'floyd-steinberg' 
  | 'atkinson' 
  | 'bayer' 
  | 'random'
  | 'jarvis-judice-ninke'
  | 'stucki'
  | 'burkes'
  | 'sierra'
  | 'sierra-lite'
  | 'halftone'
  | 'pattern';

export interface DitherSettings {
  algorithm: DitheringAlgorithm;
  colorDepth: number;
  contrast: number;
  brightness: number;
}

interface DitherStore {
  // Image state
  originalImage: HTMLImageElement | null;
  imageDataUrl: string | null;
  
  // Settings
  settings: DitherSettings;
  
  // Processing state
  isProcessing: boolean;
  
  // Actions
  setOriginalImage: (image: HTMLImageElement, dataUrl: string) => void;
  updateSettings: (settings: Partial<DitherSettings>) => void;
  setIsProcessing: (isProcessing: boolean) => void;
  reset: () => void;
}

const defaultSettings: DitherSettings = {
  algorithm: 'floyd-steinberg',
  colorDepth: 2,
  contrast: 100,
  brightness: 100,
};

export const useDitherStore = create<DitherStore>((set) => ({
  originalImage: null,
  imageDataUrl: null,
  settings: defaultSettings,
  isProcessing: false,
  
  setOriginalImage: (image, dataUrl) => set({ 
    originalImage: image, 
    imageDataUrl: dataUrl 
  }),
  
  updateSettings: (newSettings) => set((state) => ({
    settings: { ...state.settings, ...newSettings }
  })),
  
  setIsProcessing: (isProcessing) => set({ isProcessing }),
  
  reset: () => set({
    originalImage: null,
    imageDataUrl: null,
    settings: defaultSettings,
    isProcessing: false,
  }),
}));
